class ParsedTestSuiteMap {

  _keys: string[];
  _values: ParsedTestSuite[];

  constructor() {
    this._keys = [];
    this._values = [];
  }

  set(key:string, value: ParsedTestSuite): void {
    let index = this._keys.indexOf(key);
    if (index == -1) {
      this._values[this._keys.length] = value;
      this._keys.push(key);
    } else {
      this._values[index] = value;
    }
  }

  get(key:string): ParsedTestSuite {
    let index = this._keys.indexOf(key);
    if (index === -1) { return null; }
    return this._values[index];
  }

  keys(): string[] {
    return this._keys;
  }

  values(): ParsedTestSuite[] {
    return this._values;
  }

  remove(key): void {
    let index = this._keys.indexOf(key);
    if (index === -1) { return; }

    this._keys.splice(index, 1);
    this._values.splice(index, 1);
  }

}